import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Lock, Eye, EyeOff, AlertCircle, DollarSign } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { MasterPinService } from '@/utils/masterPinService';

interface ChangeCashPinDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  userId?: string;
}

export function ChangeCashPinDialog({ open, onOpenChange, userId }: ChangeCashPinDialogProps) {
  const [currentPin, setCurrentPin] = useState('');
  const [newPin, setNewPin] = useState('');
  const [confirmPin, setConfirmPin] = useState('');
  const [showCurrentPin, setShowCurrentPin] = useState(false);
  const [showNewPin, setShowNewPin] = useState(false);
  const [showConfirmPin, setShowConfirmPin] = useState(false);
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [hasExistingPin, setHasExistingPin] = useState(false);
  const { toast } = useToast();

  useEffect(() => {
    let mounted = true;
    (async () => {
      const has = await MasterPinService.hasMasterPin(userId);
      if (mounted) setHasExistingPin(has);
    })();
    return () => { mounted = false; };
  }, [userId, open]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    try {
      // التحقق من صحة البيانات المدخلة
      if (!newPin || newPin.length < 4) {
        setError('يجب أن يكون الرقم السري 4 أرقام على الأقل');
        setIsLoading(false);
        return;
      }

      if (newPin !== confirmPin) {
        setError('الرقم السري الجديد وتأكيده غير متطابقين');
        setIsLoading(false);
        return;
      }

      // إذا كان هناك رقم سري موجود، يجب التحقق منه أولاً
      if (hasExistingPin) {
        if (!currentPin) {
          setError('يرجى إدخال الرقم السري الرئيسي الحالي');
          setIsLoading(false);
          return;
        }

        const ok = await MasterPinService.verifyMasterPin(currentPin, userId);
        if (!ok) {
          setError('الرقم السري الرئيسي الحالي غير صحيح');
          setIsLoading(false);
          return;
        }
      }

      // حفظ الرقم السري الجديد
      const changed = hasExistingPin
        ? await MasterPinService.changeMasterPin(currentPin, newPin, userId)
        : await MasterPinService.createMasterPin(newPin, userId);

      if (!changed) {
        setError('تعذر حفظ الرقم السري الرئيسي');
        setIsLoading(false);
        return;
      }

      // إظهار رسالة نجاح
      toast({
        title: hasExistingPin ? "تم تغيير الرقم السري الرئيسي" : "تم تعيين الرقم السري الرئيسي",
        description: hasExistingPin 
          ? "تم تغيير الرقم السري الرئيسي بنجاح (سيتم تطبيقه على جميع النوافذ)" 
          : "تم تعيين الرقم السري الرئيسي بنجاح (سيتم تطبيقه على جميع النوافذ)",
      });

      // إغلاق النافذة وإعادة تعيين الحقول
      handleClose();
    } catch (error) {
      setError('حدث خطأ أثناء حفظ الرقم السري الرئيسي');
    } finally {
      setIsLoading(false);
    }
  };

  const handleClose = () => {
    setCurrentPin('');
    setNewPin('');
    setConfirmPin('');
    setError('');
    setShowCurrentPin(false);
    setShowNewPin(false);
    setShowConfirmPin(false);
    onOpenChange(false);
  };

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <DollarSign className="h-5 w-5 text-purple-600" />
            {hasExistingPin ? 'تغيير الرقم السري الرئيسي' : 'تعيين الرقم السري الرئيسي'}
          </DialogTitle>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-4 py-4">
          <div className="bg-blue-50 p-3 rounded-md text-sm text-blue-700 mb-4 flex items-start gap-2">
            <AlertCircle className="h-4 w-4 mt-0.5 shrink-0" />
            <p>
              هذا الرقم السري موحد لجميع العمليات الحساسة في التطبيق (الديون، التقسيط، درج النقدية، إلخ).
            </p>
          </div>

          {error && (
            <div className="bg-red-50 text-red-600 p-3 rounded-md text-sm flex items-center gap-2">
              <AlertCircle className="h-4 w-4" />
              {error}
            </div>
          )}

          {hasExistingPin && (
            <div className="space-y-2">
              <Label htmlFor="current-pin">الرقم السري الحالي</Label>
              <div className="relative">
                <Lock className="absolute right-3 top-2.5 h-4 w-4 text-gray-500" />
                <Input
                  id="current-pin"
                  type={showCurrentPin ? "text" : "password"}
                  value={currentPin}
                  onChange={(e) => setCurrentPin(e.target.value)}
                  className="pr-10 pl-10 text-left"
                  dir="ltr"
                  placeholder="****"
                />
                <button
                  type="button"
                  onClick={() => setShowCurrentPin(!showCurrentPin)}
                  className="absolute left-3 top-2.5 text-gray-500 hover:text-gray-700"
                >
                  {showCurrentPin ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                </button>
              </div>
            </div>
          )}

          <div className="space-y-2">
            <Label htmlFor="new-pin">الرقم السري الجديد</Label>
            <div className="relative">
              <Lock className="absolute right-3 top-2.5 h-4 w-4 text-gray-500" />
              <Input
                id="new-pin"
                type={showNewPin ? "text" : "password"}
                value={newPin}
                onChange={(e) => setNewPin(e.target.value)}
                className="pr-10 pl-10 text-left"
                dir="ltr"
                placeholder="****"
              />
              <button
                type="button"
                onClick={() => setShowNewPin(!showNewPin)}
                className="absolute left-3 top-2.5 text-gray-500 hover:text-gray-700"
              >
                {showNewPin ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
              </button>
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="confirm-pin">تأكيد الرقم السري الجديد</Label>
            <div className="relative">
              <Lock className="absolute right-3 top-2.5 h-4 w-4 text-gray-500" />
              <Input
                id="confirm-pin"
                type={showConfirmPin ? "text" : "password"}
                value={confirmPin}
                onChange={(e) => setConfirmPin(e.target.value)}
                className="pr-10 pl-10 text-left"
                dir="ltr"
                placeholder="****"
              />
              <button
                type="button"
                onClick={() => setShowConfirmPin(!showConfirmPin)}
                className="absolute left-3 top-2.5 text-gray-500 hover:text-gray-700"
              >
                {showConfirmPin ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
              </button>
            </div>
          </div>

          <div className="flex justify-end gap-3 mt-6">
            <Button type="button" variant="outline" onClick={handleClose}>
              إلغاء
            </Button>
            <Button type="submit" disabled={isLoading} className="bg-purple-600 hover:bg-purple-700">
              {isLoading ? 'جاري الحفظ...' : (hasExistingPin ? 'تغيير الرقم السري' : 'تعيين الرقم السري')}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
